<template>
  <div>
    <img src="../assets/fondo.jpg">
    <div>
      Total invitados: {{ totalInvitados }}
    </div>
  </div>
</template>

<script>
import { GUESTS } from '@/components/guest.js';
export default {
  data() {
    return {
      guests: GUESTS,
    };
  },
  computed: {
    totalInvitados() {
      let total = 0;
      this.guests.forEach(g => {
        total = total + g.guests.length;
      });
      return total;
    },
  },
};
</script>